@import 'sanitize.css/sanitize.css';
@import 'sanitize.css/forms.css';
@import 'sanitize.css/assets.css';
@import 'sanitize.css/typography.css';
@import 'sanitize.css/reduce-motion.css';
:root {
  --color-black: #000000;
  --color-white: #ffffff;
  --black: #000000;
  --white: #ffffff;
  --purple: #6028ff;
  --grey-dark: #282730;
  --purple-light: #d4d5ff;
  --font-size-root: 100%;
  --font-size-base: 1rem;
  --font-weight-base: 400;
  --line-height-base: 1.25;
}

:root {
  font-size: var(--font-size-root);
}

html,
body {
  height: 100%;
}

html {
  min-width: 320px;
  min-height: 100%;
  font-size: var(--font-size-root);
  font-family: sans-serif;
  line-height: normal;
  -webkit-tap-highlight-color: rgb(var(--color-black) 0);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: var(--body-color);
  font-weight: var(--font-weight-base);
  font-size: var(--font-size-base);
  font-family: var(--font-base);
  line-height: var(--line-height-base);
  background-color: var(--body-bg);
}

svg {
  display: block;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0;
}

[hidden] {
  display: none;
}

@font-face {
  font-weight: 400;
  font-family: Inter;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/Inter-Regular.woff2") format("woff2");
}
@font-face {
  font-weight: 500;
  font-family: Inter;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/Inter-Medium.woff2") format("woff2");
}
@font-face {
  font-weight: 700;
  font-family: Inter;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/Inter-Bold.woff2") format("woff2");
}
.about {
  width: 100vw;
  height: 500px;
  min-height: auto;
  background: var(--grey-dark);
}
@media screen and (min-width: 1280px) {
  .about {
    height: 2000px;
    min-height: 100vh;
  }
}

.gallery {
  width: 100vw;
  height: 500px;
  min-height: auto;
  background: lightblue;
}
@media screen and (min-width: 1280px) {
  .gallery {
    height: 100%;
    min-height: auto;
  }
}
.gallery__container-photo {
  display: flex;
  width: 100%;
  height: 100%;
}
.gallery__container-photo__photo {
  width: 780px;
  height: 100%;
  object-fit: cover;
  aspect-ratio: 1/1;
}

.hero {
  width: 100vw;
  height: 500px;
  min-height: auto;
  background: var(--purple);
}
@media screen and (min-width: 1280px) {
  .hero {
    height: 100%;
    min-height: 100vh;
  }
}

.horizontal-animation {
  overflow-x: hidden;
}
.horizontal-animation__inner, .horizontal-animation__animation {
  display: flex;
  min-height: 100vh;
}
.horizontal-animation__inner--right, .horizontal-animation__animation--right {
  display: block;
}
@media screen and (min-width: 1280px) {
  .horizontal-animation__inner--right, .horizontal-animation__animation--right {
    display: flex;
  }
}

.news {
  width: 100vw;
  height: 100%;
  min-height: auto;
  background: lightgrey;
}
@media screen and (min-width: 1280px) {
  .news {
    height: 100%;
  }
}

.planet {
  width: 100vw;
  height: 500px;
  min-height: auto;
  background: var(--white);
}
@media screen and (min-width: 1280px) {
  .planet {
    height: 100%;
    min-height: 100vh;
  }
}

.scroll-indicator {
  position: fixed;
  top: 0;
  z-index: 1;
  width: 100%;
  background: var(--purple);
}
.scroll-indicator__inner {
  width: 100%;
  height: 4px;
  background: var(--purple);
}
.scroll-indicator__progress {
  width: 0;
  height: 4px;
  background: var(--black);
}

.technologies {
  width: 100vw;
  height: 500px;
  min-height: auto;
  background: var(--purple-light);
}
@media screen and (min-width: 1280px) {
  .technologies {
    height: 100%;
  }
}

